<template>
  <v-data-table
    :headers="headers"
    :items="vehicleInfo"
    sort-by="nome"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Veículos</v-toolbar-title>

        <v-spacer></v-spacer>

        <v-dialog
          v-model="dialog"
          max-width="700px"
          shaped
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              color="primary"
              dark
              class="mb-2"
              v-bind="attrs"
              v-on="on"
            >
              Novo Cadastro
            </v-btn>
          </template>

          <!-- User Register Modal Start -->
            <v-card shaped>
              <v-card-title>{{ formTitle }}</v-card-title>
              <v-card-text>
                <v-select
                  v-model="editedItem.type"
                  :items="vehicleOptions"
                  label="Tipo do Veículo"
                  prepend-icon="mdi-truck"
                />
                <v-text-field
                  v-model="editedItem.plate"
                  label="Placa"
                  prepend-icon="mdi-numeric"
                />
                <v-text-field
                  v-model="editedItem.owner"
                  label="Proprietário"
                  prepend-icon="mdi-account-circle"
                />
              </v-card-text>
              
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="close"
                >
                  Cancel
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="save"
                >
                Save
                </v-btn>
              </v-card-actions>
            </v-card>
        </v-dialog>
        <!-- User Register Modal End -->

        <!-- Delete Modal -->
        <v-dialog v-model="dialogDelete" max-width="700px">
          <v-card>
            <v-card-title class="text-h6">Excluir Veículo</v-card-title>
            <v-card-text class="red--text">Tem certeza que deseja excluir esse veículo?</v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDisable">Cancelar</v-btn>
              <v-btn color="blue darken-1" text @click="disableVehicleConfirm">Excluir</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <!-- Delete Modal End -->

      </v-toolbar>
    </template>
    
    <template v-slot:[`item.actions`]="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
      <v-icon
        small
        class="mr-2"
        @click="disableVehicle(item)"
      >
        mdi-delete
      </v-icon>
    </template>

    <template v-slot:no-data>
      <v-btn
        color="primary"
        @click="initialize"
      >
        Reset
      </v-btn>
    </template>
  </v-data-table>
</template>


<!-- DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR DIVISOR -->

<script>
  export default {
    name: 'VehicleList',
    data: () => ({
      dialog: false,
      dialogDelete: false,
      menu: false,
      search: '',
      vehicleOptions: ['Cavalo', 'Carreta'],
      required: [
        v => !!v || 'Este campo é obrigatório',
      ],
      headers: [
        {
          text: 'Placa',
          align: 'start',
          sortable: true,
          value: 'plate',
        },
        { text: 'Tipo', value: 'type' },
        { text: 'Proprietário', value: 'owner' },
        { text: '', value: 'actions', sortable: false },
      ],
      vehicleInfo: [],
      editedIndex: -1,
      editedItem: {
        plate: '',
        type: '',
        owner: '',
      },
      defaultItem: {
        plate: '',
        type: '',
        owner: '',
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Cadastrar Veículo' : 'Editar Veículo'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDisable()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.vehicleInfo = [
          {
            plate: 'ABC1D234',
            type: 'Carreta',
            owner: 'P S DE ALMEIDA',
          },
        ]
      },

      editItem (item) {
        this.editedIndex = this.vehicleInfo.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      disableVehicle (item) {
        this.editedIndex = this.vehicleInfo.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      disableVehicleConfirm () {
        this.vehicleInfo.splice(this.editedIndex, 1)
        this.closeDisable()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDisable () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.vehicleInfo[this.editedIndex], this.editedItem)
        } else {
          this.vehicleInfo.push(this.editedItem)
        }
        this.close()
      },
    },
  }
</script>